              ><span>{{ item.categoryName }}</span></a
            >
          </li>

          <li @click="$router.push('allCategories')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../common/images/icon_manue_10.png"/></span
              ><span>View all categories</span></a
            >
          </li>
        </ul>
        <ul>
          <li @click="$router.push('orderList')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../common/images/icon_manue_11.png" /><em
                  class="bag"
                >
                  <i class="font10"> 99 </i></em
                ></span
              ><span>Orders</span></a
            >
          </li>
          <li @click="$router.push('whislist')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../common/images/icon_manue_12.png"/></span
