              ><span class="imgWrap"
                ><img src="../common/images/icon_manue_13.png"
              /></span>
              <span>My Acount</span></a
            >
          </li>
          <li @click="$router.push('mystores')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../common/images/icon_manue_14.png"
              /></span>
              <span>My Store</span></a
            >
          </li>
          <li @click="$router.push('browseRecord')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
