            <img src="static/images/icon_share_01.png" />
            <p class="name">WhatsApp</p>
          </li>
          <li>
            <img src="static/images/icon_share_02.png" />
            <p class="name">Twitter</p>
          </li>
          <li>
            <img src="static/images/icon_share_03.png" />
            <p class="name">Linkedin</p>
          </li>
          <li>
            <img src="static/images/icon_share_04.png" />
            <p class="name">Facebook</p>
          </li>
          <li>
            <img src="static/images/icon_share_05.png" />
            <p class="name">Ins</p>
          </li>
          <li>
            <img src="static/images/icon_share_06.png" />
            <p class="name">Messenger</p>
          </li>
          <li>
