    <!-- 回到顶部 -->
    <div class="go-top iconfont" v-if="btnFlag" @click="backTop">
      &#xe612;
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "home",
  data() {
    return {
      btnFlag: false,
      show: false,
      // 购物数量
      num: 1,
      colorSelect: null,
      sizeSelect: null,
      // 规格属性
      skuCode: null,
      searchValue: "",
      bannerSwiperOption: {
        autoplay: true,
        loop: true,
        slidesPerView: 1.2,
        spaceBetween: 0,
        centeredSlides: true,
        onClick: function(swiper, event) {
          console.log(swiper, event);
        }
      },
      flashSwiperOption: {
        slidesPerView: 3.5,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      commonSwiperOption: {
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      shareVisible: false,
      timer: {
        d: "00",
        h: "00",
        m: "00",
        s: "00"
      },
      chartData: {},
      colorList: [
        { color: "White", bol: true },
        { color: "Black", bol: false },
        { color: "Red", bol: false },
        { color: "Blue", bol: false }
      ],
    };
  },
  computed: {
    ...mapGetters(["indexData", "productData", "user", "categoryOne"])
  },
  created() {
    //console.log(this.productData.flashSale.productList)
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
    this.init();
  },
  destroyed() {
    window.addEventListener("scroll", this.scrollToTop);
  },
  methods: {
    init() {
      let t = this;

      this.$store.dispatch("productData").then(r => {
        this.timer.remain = r.flashSale.endTimeStr * 1;
        t.countTime();
      });
    },
    countTime() {
      let leftTime = this.timer.remain;
      if (leftTime >= 0) {
        this.timer.d = this.timeStr(Math.floor(leftTime / 60 / 60 / 24));
        this.timer.h = this.timeStr(Math.floor((leftTime / 60 / 60) % 24));
        this.timer.m = this.timeStr(Math.floor((leftTime / 60) % 60));
        this.timer.s = this.timeStr(Math.floor(leftTime % 60));
        this.timer.remain = this.timer.remain - 1;
      }

      //递归每秒调用countTime方法，显示动态时间效果
      setTimeout(this.countTime, 1000);
    },
    timeStr(count) {
      if (count < 10) {
        return "0" + count;
      } else {
        return count;
      }
    },
    // 弹出规格
    addChart(data) {
      this.$refs.addCart.load(data);

    },
    shareShow(data) {
      this.shareVisible = true;
    },
    goDetail(spuCode) {
      this.$router.push({
        path: "/proDetail",
        query: { spuCode: spuCode }
      });
    },
    goCart() {
      this.$router.push({
        path: "/cart",
        query: {}
      });
    },
    changeBanner(e) {
      let index = this.$refs.bannerSwiper.swiper.clickedIndex;
      this.to(this.indexData.bannerAdList[index].url);
    },
    chooseBest(e) {
      console.log(e);
      let index = this.$refs.bestSaleSwiper.swiper.clickedIndex;
      this.goDetail(this.productData.bestSales[index].spuCode);
    },
    chooseDetail(spoCode){
        this.goDetail(spoCode);
    },
    to(url) {
      window.location.href = url;
    },
    //回到顶部
    backTop() {
      let that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },
    scrollToTop() {
      let that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 60) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/home.scss";
</style>



